import React,{useState} from 'react'

function TextForm(props) {
  const [text,setText] = useState("Enter text here");
  const handleOnChange = (e)=>{
      setText(e.target.value);
  }
  const handleOnUp = () =>{
    setText(text.toUpperCase())
    props.sAlert("Converted To Uppercase","success")
  }
  
  const handleOnLo = () =>{
    setText(text.toLowerCase())
    props.sAlert("Converted To Lowercase","success")
  }

  const handleOnCl = () =>{
    setText('');
    props.sAlert("Cleared","success")
  }
  return (
    <div className={`bg-${props.mode}`}>
      <div className="container">
        <br />
        <h3 className={`text-${props.mode==='dark'?'light':'dark'}`}>{props.heading}</h3>
        <br />
        <div className="mb-3">
          <textarea value={text} onChange={handleOnChange} className='form-control border-0 shadow-lg outline-none'  name="" id="" cols="30" rows="10"></textarea>
        </div>
        <button className={`btn btn-${props.mode==='light'?'dark':'light'} m-2 shadow`} onClick={handleOnUp}>UPPERCASE</button>
        <button className={`btn btn-${props.mode==='light'?'dark':'light'} m-2 shadow`} onClick={handleOnLo}>LOWERCASE</button>
        <button className={`btn btn-${props.mode==='light'?'dark':'light'} m-2 shadow`} onClick={handleOnCl}>CLEAR</button>
      </div>
      <br />
      <div className={`container shadow-lg py-1 px-3 rounded text-${props.mode==='dark'?'light':'dark'}`}>
        <h6>WORDS : {text.split(" ").filter((e)=>{return e.length!==0}).length}</h6>
        <h6>CHARACTERS : {text.length}</h6>
        <h6>{text.split(" ").filter((e)=>{return e.length!==0}).length * 0.008} MINUTES TO READ</h6>
      </div>
      <br />
      <div className={`container py-1 px-3 shadow-lg text-${props.mode==='dark'?'light':'dark'}`}>
        <h5><li>PREVIEW</li></h5>
        <h6>{text}</h6>
      </div>
    </div>
  )
}
export default TextForm